.dialog {
    position: fixed;
    z-index: 200;
    width: 100%;
    height: 100%;
    background-color: #ffffff9f;
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.5s;
    display: flex;
    justify-content: center;
    align-items: center;
}

.dialog_open {
    opacity: 1;
    pointer-events: auto;
}

.dialog__inner {
    background: #fff;
    padding: 20px 85px 60px 60px;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.3);
    position: relative;
    min-width: 425px;
}

.dialog__header {
    font-size: 4vw;
    margin-bottom: 30px;
}

.contact-form-dialog__header {
    margin: 30px 0 40px;
}

.dialog__form {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.dialog__input-field {
    border: none;
    box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.25);
    font-size: 25px;
    padding: 15px 20px;
    margin-bottom: 30px;
    align-self: stretch;
    transition: all 0.3s;
}

.dialog__input-field_mr {
    margin-right: 20px;
}

.dialog__input-field_tall {
    height: 150px;
}

.dialog__input-field:focus {
    border: none;
    background: #d3d3d35c;
    outline: none;
}

.dialog__submit-btn {
    cursor: pointer;
    margin-top: 20px;
    align-self: flex-start;
}

.dialog__submit-btn:hover {
    transform: none;
}

.dialog__submit-btn:not(:disabled):hover {
    background-color: #da4025;
}

.dialog__submit-btn:disabled {
    opacity: 0.8;
}

.dialog__close-btn {
    position: absolute;
    top: 25px;
    right: 25px;
    border: none;
    background: none;
    padding: 0;
    cursor: pointer;
}

.dialog__close-btn-img {
    width: 25px;
    height: 25px;
    min-width: 18px;
    min-height: 18px;
}

@media (max-width: 768px) {
    .dialog__inner {
        width: 100vw;
        min-width: 100vw;
        height: 100vh;
        box-sizing: border-box;
        overflow-x: hidden;
        overflow-y: scroll;
        padding: 40px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: baseline;
        align-content: flex-start;
    }
    .dialog__close-btn {
        position: static;
    }
    .dialog__close-btn-img {
        width: 4.297vw;
        height: 4.297vw;
    }
    .dialog__header {
        font-size: 5.625vw;
        margin: 0;
    }
    .dialog__form {
        width: 100%;
        order: 1;
        padding-top: 40px;
    }
    .form-group {
        width: 100%;
        box-sizing: border-box;
    }
    .dialog__input-field {
        width: 100%;
        box-sizing: border-box;
        font-size: inherit;
    }
}
@media (max-width: 425px) {
    .dialog__inner {
        padding: 20px;
    }
    .dialog__header {
        font-size: 6.7vw;
    }
}
